<template lang="pug">
    v-card 
        v-card-text
            v-card-title
                span 更新运行中的交易对参数
            v-container
                v-row 
                    v-col(cols="12" sm="6")
                        v-text-field(outlined hide-details v-model="tarCoin" disabled label="数字币")
                    v-col(cols="12" sm="6")
                        v-text-field(outlined hide-details v-model="baseCoin" disabled label="基础币")
                    v-col(cols="12" sm="6")
                        v-text-field(outlined hide-details v-model="threshold" disabled label="挂单阈值")
                    v-col(cols="12" sm="6")
                        v-text-field(outlined hide-details v-model="orderAmount" label="下单数量")
                    v-col(cols="12" sm="6")
                        v-text-field(outlined hide-details v-model="profit" disabled label="利润")
                    v-col(cols="12" sm="6")
                        v-text-field(outlined hide-details v-model="sepseconds" label="刷新间隔(秒)")
                    
                    
                    
            v-card-actions
                v-btn(color="success" dark @click="update") 更新
</template>

<script>
import * as api from "@/apis/wswebapi"
import { get} from 'vuex-pathify'
export default {
    data: () => ({
    // tarCoin: "usdt",
    // baseCoin: "cny",
    // threshold:0.002,
    orderAmount:10,
    // profit:0.003,
    sepseconds:5,
    ExName:"WKJ",
    }),
    computed: {
    upexconfig:get('exbot/upexconfig'),
    tarCoin(){
        return this.upexconfig.tarcoin
    },
    baseCoin(){
        return this.upexconfig.basecoin
    },
    profit(){
        return this.upexconfig.profit
    },
    threshold(){
        return this.upexconfig.threshold
    },
    email:get('user/email'),
    isLogin:get('user/islogin'),
    username:get('user/username'),
    exbotisexpire:get('user/exbotisexpire'),
    },
    methods:{
        update(){
            if (!this.isLogin){
                this.$store.dispatch("msglog/push_msg",{message:"用户未登陆，请登陆后重试",type:"error"});
                return
            }
            if (this.exbotisexpire){
                this.$store.dispatch("msglog/push_msg",{message:"软件已过期，请联系管理员",type:"error"});
                return
            }

            let params = {
                "username": this.username,
                "exname": this.ExName,
                "tarcoin": this.tarCoin,
                "basecoin": this.baseCoin,
                "threshold": Number(this.threshold),
                "leverage": 0,
                "orderamount": Number(this.orderAmount),
                "profit": Number(this.profit),
                "notifyemail": this.email,
                "sepseconds": Number(this.sepseconds)
            }
            api.DBMPutOne(params).then(res=>{
                if (res.code==200){
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"success"});
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"})
                }
            })
        }
    }
}
</script>